<template>
  <div class="nav">
    <div class="nav-menu">
      <div>
        <transition name="fade">
          <p
            v-if="!menuActive"
            @click="activateMenu"
            @mouseover="isHover(true, 'menu')"
            @mouseleave="isHover(false, 'menu')"
          >
            MENU
          </p></transition
        >
      </div>
      <NavWrap @close-menu="deactivateMenu" :active="menuActive" />
      <transition name="fade">
        <div
          class="button-close"
          @click="deactivateMenu"
          v-if="menuActive"
          @mouseover="isHover(true, 'menu')"
          @mouseleave="isHover(false, 'menu')"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            width="40px"
            height="40px"
          >
            <path
              fill-rule="evenodd"
              fill="rgb(255, 255, 255)"
              d="M39.91,36.263 L36.263,39.91 L20.0,22.828 L3.736,39.91 L0.908,36.263 L17.171,20.0 L0.908,3.736 L3.736,0.908 L20.0,17.171 L36.263,0.908 L39.91,3.736 L22.828,20.0 L39.91,36.263 Z"
            />
          </svg>
        </div>
      </transition>
    </div>
    <div v-if="device == 'desktop' && windowWidth > 860" class="left hoverable">
      <router-link to="/#home" @click="$emit('change-page', 'home')">
        <div
          class="button-container"
          @mouseover="isHover(true, 'home')"
          @mouseleave="isHover(false, 'home')"
        >
          <a :style="computedTopHome" href="/" class="nav-button">MAX</a>
          <a :style="computedTopHome" href="/" class="nav-button">HOME</a>
        </div>
        ©2022
      </router-link>
    </div>
    <div v-if="device == 'desktop' && windowWidth > 860" class="right" id="nav-right">
      <router-link to="/#info"
        ><div
          class="button-container"
          style="margin-right: 40px"
          @mouseover="isHover(true, 'work')"
          @mouseleave="isHover(false, 'work')"
        >
          <a :style="computedTopWork" href="/" class="nav-button"
            >WORK WITH ME</a
          >
          <a href="/" :style="computedTopWork" class="nav-button"
            >COMMISSIONS</a
          >
        </div></router-link
      >
      <router-link to="/#tos"
        ><div
          class="button-container"
          @mouseover="isHover(true, 'tos')"
          @mouseleave="isHover(false, 'tos')"
        >
          <a :style="computedTopTos" href="/" class="nav-button">TOS</a>
          <a :style="computedTopTos" href="/" class="nav-button">READ</a>
        </div></router-link
      >
    </div>
    
  </div>
</template>

<script>
import NavWrap from "./nav/navigationContainer.vue";

export default {
  name: "NavigationBar",
  props: ["activePage", "device", "windowWidth"],
  components: {
    NavWrap,
  },
  data() {
    return {
      topWork: 0,
      topTos: 0,
      menuActive: false,
    };
  },
  computed: {
    computedTopWork() {
      return `transform: translateY(${this.topWork}%)`;
    },
    computedTopTos() {
      return `transform: translateY(${this.topTos}%)`;
    },
    computedRoute() {
      return this.activePage == "home";
    },
    computedTopHome() {
      return this.computedRoute
        ? `transform: translateY(0%)`
        : `transform: translateY(-100%)`;
    },
    /* computedLink() {

    } */
  },
  methods: {
    isHover(bool, el) {
      this.$root.$refs.cursor.expandCursor(bool);
      this.changeText(bool, el);
    },
    doThing() {
      if (this.$route.name == "home") {
        window.scroll({ top: 0, behavior: "smooth" });
      } else {
        this.$router.push("/");
      }
    },
    activateMenu() {
      this.menuActive = true;
    },
    deactivateMenu() {
      this.menuActive = false;
    },
    changeText(bool, el) {
      if (bool) {
        if (el == "work") {
          this.topWork = -100;
        } else if (el == "tos") {
          this.topTos = -100;
        }
      } else {
        if (el == "work") {
          this.topWork = 0;
        } else if (el == "tos") {
          this.topTos = 0;
        }
      }
    },
  },
  mounted() {},
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900");

a,
a:active,
a:hover,
a:visited {
  color: white;
}

.button-container {
  display: inline-flex;
  flex-direction: column;
  overflow: hidden;
  max-height: 19px;
}

.nav {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  z-index: 5;
  color: #f7f8fa;
}

.nav .left {
  color: black;
  position: fixed;
  top: 60px;
  left: 60px;
}

.nav-menu {
  position: fixed;
  left: 50%;
  top: 60px;
  height: 40px;
  transform: translate(-50%, 0);
}

.nav-menu p {
  color: black;
  font-weight: 600;
  background: white;
  width: 60px;
  margin: 0;
  padding: 5px 10px;
  box-shadow: 0 0 30px 30px #10101070;
  cursor: pointer;
}

.nav-menu > div:first-child {
  width: 80px;
  margin: auto;
  height: 29px;
}

.button-close {
  position: fixed;
  position: fixed;
  left: 50%;
  top: 0px;
  transform: translate(-50%, 0);
  cursor: pointer;
}

.nav a {
  /* color: black; */
  text-decoration: none;
}

.nav a:visited {
  color: #f7f8fa;
}

.nav .right {
  align-items: right;
  display: flex;
  position: fixed;
  top: 60px;
  right: 60px;
}

.home {
  font-weight: 500;
}

.nav .nav-button {
  font-weight: 500;
  position: relative;
  transition: transform 0.5s ease;
}

.nav .nav-button:hover {
  cursor: pointer;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-to,
.fade-leave-from {
  opacity: 1;
}
</style>