<template>
  <div :style="styleObject" class="nav-wrap">
    <div class="menu">
      <div class="info">
        <div class="button">
          <sup>01</sup>
          <span
            :style="elementHover[0] ? {} : { width: `0` }"
            class="break"
          ></span>
          <p @click="handleClick('/#info')" @mouseover="isHover(true, 0)" @mouseleave="isHover(false, 0)">
            INFORMATION
          </p>
        </div>
        <div class="button">
          <sup>02</sup>
          <span
            :style="elementHover[1] ? {} : { width: `0` }"
            class="break"
          ></span>
          <p @click="handleClick('/portfolio')" @mouseover="isHover(true, 1)" @mouseleave="isHover(false, 1)">
            PORTFOLIO
          </p>
        </div>
        <div class="button">
          <sup>03</sup>
          <span
            :style="elementHover[2] ? {} : { width: `0` }"
            class="break"
          ></span>
          <p @click="handleClick('/#work')" @mouseover="isHover(true, 2)" @mouseleave="isHover(false, 2)">
            WORK WITH ME
          </p>
        </div>
        <div class="button">
          <sup>04</sup>
          <span
            :style="elementHover[3] ? {} : { width: `0` }"
            class="break"
          ></span>
          <p @click="handleClick('/#tos')" @mouseover="isHover(true, 3)" @mouseleave="isHover(false, 3)">
            TERMS OF SERVICE
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavWrap",
  props: ["active"],
  data() {
    return {
      styleObject: {
        transform: `translateY(100%)`,
      },
      elementHover: [false, false, false, false],
    };
  },
  watch: {
    active(newVal) {
      newVal
        ? (this.styleObject.transform = `translateY(0)`)
        : (this.styleObject.transform = `translateY(100%)`);
    },
  },
  methods: {
    isHover(bool, el) {
      this.$root.$refs.cursor.expandCursor(bool);
      this.hoverElement(bool, el);
    },
    hoverElement(bool, el) {
      this.elementHover[el] = bool;
    },
    handleClick(route) {
        this.$emit('close-menu');
        this.$router.push(route);
    }   
  },
};
</script>

<style scoped>
.nav-wrap {
  height: 100vh;
  width: 100vw;
  margin-top: -89px;
  transition: transform 0.7s ease;
  background: rgba(22, 22, 22, 0.95);
}

.menu {
  margin: auto;
  padding-top: 250px;
}

.info {
  display: inline-flex;
  flex-direction: column;
  margin: auto;
}

p {
  font-size: 72px;
  font-weight: 800;
  margin: 0;
  margin-bottom: 30px;
  text-align: left;
}

p:hover {
  font-style: italic;
  cursor: pointer;
}

.button {
  display: inline-flex;
}

.break {
  display: inline-flex;
  height: 2px;
  width: 100px;
  margin: 40px 20px 40px 0;
  background: white;
  transform-origin: left;
  transition: width 0.3s ease;
}

sup {
  font-size: 18px;
  margin-top: 10px;
  margin-right: 15px;
}
</style>